@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Open Sans Condensed', sans-serif;
  padding: 20px 60px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  text-decoration: none;
  color: black;
}

*{
  box-sizing: border-box;
}
.homePage{display:flex;flex-direction:column;align-items:center;padding:20px 80px}

.home-menu{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}@media (max-width: 450px){.home-menu{width:20rem}}

.menu-item{min-width:30%;height:240px;flex:1 1 auto;display:flex;align-items:center;justify-content:center;border:1px solid black;margin:0 7.5px 15px;overflow:hidden}.menu-item:hover{cursor:pointer}.menu-item:hover .bgImage{-webkit-transform:scale(1.1);transform:scale(1.1);transition:-webkit-transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95), -webkit-transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item:hover .content{opacity:0.9}.menu-item.large{height:380px}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .bgImage{height:100%;width:100%;background-position:center;background-size:cover}.menu-item .content{height:90px;padding:0 25px;display:flex;flex-direction:column;align-items:center;justify-content:center;border:1px solid black;background-color:white;opacity:0.7;position:absolute}.menu-item .content .title{font-weight:bold;margin-bottom:6px;font-size:22px;color:#4a4a4a}.menu-item .content .subtitle{font-weight:lighter;font-size:16px}@media (max-width: 450px){.menu-item,.bgImage{min-width:9rem}}


.collectionPreview{display:flex;flex-direction:column;margin-bottom:30px}.collectionPreview .title{font-size:28px;margin-bottom:25px}.collectionPreview .preview{display:flex;justify-content:space-between}

.collectionItem{width:22vw;display:flex;flex-direction:column;height:350px;align-items:center;position:relative;margin:0.4rem}.collectionItem .itemImage{width:100%;height:95%;background-size:cover;background-position:center;margin-bottom:5px}.collectionItem .button{width:80%;opacity:0.7;position:absolute;top:255px;display:none}.collectionItem:hover .itemImage{opacity:0.8}.collectionItem:hover .button{opacity:0.85;display:flex}.collectionItem .itemFooter{width:100%;height:5%;display:flex;justify-content:space-between;font-size:18px}.collectionItem .itemFooter .itemName{width:90%;margin-bottom:15px}.collectionItem .itemFooter .itemPrice{width:10%}@media (max-width: 450px){.collectionItem{min-width:16rem;margin-bottom:1.5rem}}

.button{min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-family:'Open Sans Condensed';font-weight:bolder;border:none;cursor:pointer;display:flex;justify-content:center}.button:hover{background-color:white;color:black;border:1px solid black}.button.google-sign-in{background-color:#4285f4;color:white}.button.google-sign-in:hover{background-color:#357ae8;border:none}.button.inverted{background-color:white;color:black;border:1px solid black}.button.inverted:hover{background-color:black;color:white;border:none}

.collections{display:flex;flex-direction:column}

.category{display:flex;flex-direction:column}.category .title{font-size:38px;margin:0 auto 30px}.category .items{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:10px}.category .items .collection-item{margin-bottom:30px}@media (max-width: 450px){.items,.category{display:flex !important;flex-wrap:wrap}}

.cart-icon{width:45px;height:45px;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer}.cart-icon .shopping-icon{width:24px;height:24px}.cart-icon .item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px}

.cart-dropdown{position:absolute;width:240px;height:340px;display:flex;flex-direction:column;padding:20px;border:1px solid black;background-color:white;top:90px;right:40px;z-index:5}.cart-dropdown .empty-message{font-size:18px;margin:50px auto}.cart-dropdown .cart-items{height:240px;display:flex;flex-direction:column;overflow:scroll}.cart-dropdown button{margin-top:auto}

.cart-item{width:100%;display:flex;height:80px;margin-bottom:15px}.cart-item img{width:30%}.cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:10px 20px}.cart-item .item-details .name{font-size:16px}

.header{height:70px;width:100%;display:flex;justify-content:space-between;margin-bottom:25px}.header .logo{height:100%;width:70px;padding:3px 20px}.header .options{width:50%;height:100%;display:flex;align-items:center;justify-content:flex-end}.header .options .option{padding:10px 15px;cursor:pointer}

.identify{width:850px;display:flex;justify-content:space-between;margin:30px auto}@media (max-width: 450px){.identify{margin-left:-1.5rem;flex-wrap:wrap;flex-direction:column}}

.sign-in{width:380px;display:flex;flex-direction:column}.sign-in .title{margin:10px}.sign-in .buttons{display:flex;justify-content:space-between}

.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:white;color:grey;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid grey;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-14px;font-size:12px;color:#000}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:grey;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:12px;color:#000}

.sign-up{display:flex;flex-direction:column;width:380px}.title{margin:10px 0}

.checkout-page{width:55%;min-height:90vh;display:flex;flex-direction:column;align-items:center;margin:50px auto 0}.checkout-page .checkout-header{width:100%;padding:10px 0;display:flex;justify-content:space-between;border-bottom:1px solid darkgrey}.checkout-page .checkout-header .header-block{text-transform:capitalize;width:23%}.checkout-page .checkout-header .header-block:last-child{width:8%}.checkout-page .total{margin-top:30px;margin-left:auto;font-size:36px}.checkout-page .test-warning{text-align:center;margin-top:40px;font-size:24px;color:red}

.checkout-item{width:100%;display:flex;min-height:100px;border-bottom:1px solid darkgrey;padding:15px 0;font-size:20px;align-items:center}.checkout-item .image-container{width:23%;padding-right:15px}.checkout-item .image-container img{width:100%;height:100%}.checkout-item .name,.checkout-item .quantity,.checkout-item .price{width:23%}.checkout-item .quantity{display:flex}.checkout-item .quantity .arrow{cursor:pointer}.checkout-item .quantity .value{margin:0 10px}.checkout-item .remove-button{padding-left:12px;cursor:pointer}

.pay-button{margin-left:auto;margin-top:50px}


